div.Play {
	color: white;
	width: 15px;
	height: 15px;
	margin-left: 11px;
	border-right: solid 2px white;
	border-top: solid 2px white;
		transform: rotate(45deg) translate(-2px, 2px);
	transition: 0.2s;
  }

  div.Play:after {
	content: '';
	position: absolute;
	color:white;
	width: 1px;
	height: 20px;
	border-left: solid 2px white;
			transform: rotate(-45deg) translate(2px, -3px);
  }
  
  div.Pause {
	color: white;
	width: 12px;
	height: 12px;
	margin-left: 14px;
	border-right: solid 2px white;
	border-left: solid 2px white;
	transition: 0.2s;
  }
  
  div.Save {
	position: absolute;
	margin-left: 11px;
	margin-top: 3px;
	width: 18px;
	height: 5px;
	border-radius: 1px;
	border: solid 2px white;
	border-top: none;
  }
  
  div.Save:before {
	content: '';
	position: absolute;
	left: 6px;
	top: -11px;
	width: 2px;
	height: 11px;
	background-color: white;
  }
  
  div.Save:after {
	content: '';
	position: absolute;
	left: 4px;
	top: -6px;
	width: 6px;
	height: 6px;
	border-top: solid 2px white;
	border-right: solid 2px white;
	transform: rotate(135deg);
  }

#SaveBtn:hover div.Save:before {
	-webkit-animation: save-before-animation 0.5s ease-in alternate infinite;
	        animation: save-before-animation 0.5s ease-in alternate infinite;
}

#SaveBtn:hover div.Save:after {
	-webkit-animation: save-after-animation 0.5s ease-in alternate infinite;
	        animation: save-after-animation 0.5s ease-in alternate infinite;
}
  
