:root {
  --primary: #273238;
  --primary-light: #3d4f58;
  --primary-dark:#111618;
  --secondary:#2d749a;
  --secondary-light: #47b6f2;
  --tertiary: #ea9b80;
  --tertiary-light: #fab8a2;
  --text-light: #ebebeb;
  --text-dark: #333;
  --standard-unit: 2rem;
  --little-unit: 1rem;
  --large-unit: 5rem;
}

::-webkit-scrollbar {
  width: 1rem;
  width: var(--little-unit);
}

::-webkit-scrollbar-track {
  background: #2d749a;
  background: var(--secondary);
  border-radius: 5px;
  box-shadow: inset 1px 1px 2px #133242, 
              inset -1px -1px 2px #47b6f2;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  width:calc(2rem/2);
  width:calc(var(--standard-unit)/2);
  background: #273238;
  background: var(--primary);
  box-shadow: inset 1px 1px 2px #111618, 
              inset -1px -1px 2px #3d4f58;
}

*, *:focus {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

