button.CTA, div.CTA {
	font-weight: 700;
	background-color: #2d749a;
	background-color: var(--secondary);
	background: linear-gradient(145deg, #307ca5, #29688b);
	transition: 0.2s ease-in;
}

button.CTA:hover, div.CTA:hover {
	color: white;
	background: linear-gradient(145deg, #29688b, #307ca5);
	transition: 0.2s ease-in;
}
